export default [
    {
        name: 'gotoDetailView',
        context: 'single',
        text: '',
        execute: (context, { history }) => history.push({
            name: `${context.getModuleContext().getModuleName()}Detail`,
            params: { id: context.getFieldValue('id') },
        }),
    },
    {
        name: 'deleteList',
        context: 'batch',
        text: '',
        danger: true,
        execute: (context) => context.deleteList && context.deleteList(context.getValue(), () => context.reload()),
    },
];
